
.App {
  display: flex;
  background-color: #202a30;
  min-height: 100vh;
  overflow: hidden;
}

.control-panel {
  padding: 40px;
  z-index: 10;
  font-size: 20px;
  background-color: orange;
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border: solid 1px black;

  .control {
    //display: grid;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 120px;
    align-items: center;
    padding: 40px;
    //justify-items: center;
    border: solid 3px black;
    border-radius: 30px;
    background-color: lightgray;
    grid-row-gap: 40px;

    .weight-control {
      display: flex;
      justify-content: space-around;
      height: 20px;
      align-items: center;

      .weight-input {
        border-radius: 10px;
        margin-right: 20px;
        padding-left: 10px;
        height: 50px;
        width: 100px;
        font-size: 22px;
      }

      .plus-minus {
        display: grid;
        grid-row-gap: 3px;
      }
    }

    .control-second-row {
      display: flex;
      justify-content: space-evenly;

      .cover-button {
        display: flex;
        align-items: center;
        justify-content: center;
        z-index: 10;
        width: 130px;
        line-height: 50px;
        cursor: pointer;
        border-radius: 30px;
        background-color: white;
        padding: 3px;
      }

    }
  }

  .cover-buttons {
    display: flex;
    justify-content: space-between;
  }

  .data {
    display: grid;
    grid-row-gap: 5px;
    padding: 20px;
    font-style: italic;
    border: solid 3px black;
    background-color: greenyellow;
    border-radius: 30px;
    font-family: "Digital", source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
    font-size: 20px;
  }
}


.regular-button {
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10;
  width: 100px;
  line-height: 50px;
  cursor: pointer;
  //border: solid 3px black;
  border-radius: 30px;
  background-color: white;
  padding: 3px;
}

.control-button {
  @extend .regular-button;
  width: 50px;
  height: 20px;
}


.Display {
  z-index: 1;
  border-radius: 30px;
  padding: 15px;
  position: fixed;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  top: calc(50% - 4px);
  left: calc(50% + -1px);
  width: 206px;
  height: 204px;

  &.flipped {
    top: calc(50% + 4px);
    left: calc(50%);
  }

  .screen {
    display: grid;
    font-family: "Digital";
    letter-spacing: 1px;
    width: 100%;
    height: 100%;
    grid-template-columns: 10% 10% 10% 10% 10% 10% 10% 10% 10% 10%;
    grid-template-rows: 10% 10% 10% 10% 10% 10% 10% 10% 10% 10%;
  }

  &.On {
    background-color: greenyellow;
    color: black;
  }

  &.Off {
    background-color: darkolivegreen;
    color: darkgrey;
  }
}

.base-unit {
  height: 27px;

  &.Off {
    visibility: hidden;
  }
}

.oz {
  @extend .base-unit;
  margin-left: -8px;
  grid-column: 3;
  grid-row: 9;
}

.g {
  @extend .base-unit;
  margin-left: -8px;
  grid-column: 6;
  grid-row: 9;
}

.units {
  @extend .base-unit;
  height: 28px;
  //justify-self: end;
  //margin-left: 10px;
  margin-left: -12px;
  grid-column: 7;
  grid-row: 9;
}

.display-amount {
  display: grid;
  grid-template-columns: auto auto auto auto auto;
  //grid-column-gap: 5px;
  //justify-items: center;
  font-size: 75px;
  place-self: center;
  grid-column: 1 /span 10;
  grid-row: 5 / span 2;

  .amount-digit {
    min-width: 30px;
  }
}

.sign {
  font-family: 'Roboto', sans-serif;
  font-size: 45px;
  place-self: center;
  grid-column: 1;
  grid-row: 4 / span 3;
}

.battery {
  //place-self: center;
  grid-column: 8;
  grid-row: 1;
  margin-top: 5px;
  margin-left: 2px;
  height: 12px;
}

.total {
  font-family: sans-serif;
  font-size: 11px;
  font-weight: bold;
  //place-self: center;
  grid-column: 10;
  margin-top: 9px;
  margin-left: -3px;
  grid-row: 6;

  &.invisible {
    visibility: hidden;
  }
}

@keyframes fill {
  to {
    background-size: 100% 0;
  }
}

.cover {
  position: fixed;
  height: 600px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.animation {
  background: transparent linear-gradient(white,black) no-repeat 0 0;
  opacity: 0.3;
  mix-blend-mode: multiply;
  background-size: 100% 100%;
  -webkit-animation: fill 2s forwards;
  animation: fill 2s forwards;
}

.cnh_holding .power-button {
  @extend .animation;
}

.cnh_holding .display-button {
  @extend .animation;
}


.key {
  font-weight: bold;
  font-style: normal;
  font-size: 22px;
}

.battery-switch {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 4px;
  padding-bottom: 10px;
  margin-left: 5px;
  background-color: white;
  //border: solid 1px black;
  border-radius: 30px;

  input {
    margin-top: 8px;
    cursor: pointer;
  }
}

.invisible-button {
  z-index: 10;
  background-color: transparent;
  border-radius: 15px;
  position: fixed;
  height: 54px;
  width: 54px;
}

.display-button {
  @extend .invisible-button;

  &.--regular {
    top: calc(50% + 70px);
    left: calc(50% + 190px);
    transform: translate(-50%, -50%);
  }

  &.--inverted {
    top: calc(50% - 70px);
    left: calc(50% - 190px);
    transform: translate(-50%, -50%);
  }
}

.power-button {
  @extend .invisible-button;

  &.--regular {
    top: calc(50% + 183px);
    left: calc(50% + 78px);
    transform: translate(-50%, -50%);
  }

  &.--inverted {
    top: calc(50% - 183px);
    left: calc(50% - 78px);
    transform: translate(-50%, -50%);
  }
}
